import Image from '../../Elements/Media/Image'
const Media = ({ itemData, wallID, ownerId }) => {
  const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
  const isYoutubePost = (itemData.network.id === 7) ? true : false
  const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
  const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_wt_multi_images_ico_bottom' : 'tb_wt_multi_images_ico'
  const isAudio = itemData.isAudio === 1 ? true : false

  return <div className="tb_wt_media_wrap">
    {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
    {mediaType ? isYoutubePost ? <div className="tb_wt_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
      isAudio ? <div className="tb_wt_audio_ico tb__icon tb-audio"><div></div></div> :
        <div className="tb_wt_video_ico tb__icon tb-video"><div></div></div> : ''}
    <Image ImageClass="tb_wt_image" data={itemData} wallID={wallID} ownerId={ownerId} size={false} />
  </div>
}
export default Media