import Card from "./Elements/Card";

const WidgetTheme = ({ postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, onClickToCTA }) => {
  return <div className="tb_wt_post_container">
    {((postData && postData.length > 0)) && postData.map((item, index) => {
        const cardData = completeDataObject[item];
        return <Card ownerId={wall.Wall.owner} itemData={cardData} key={`wdt_crd_${index}_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} />
      })}
  </div>
}
export default WidgetTheme
